export default {
	data() {
	return{
		name:"",
		sno:"",
		head_portrait:"",
	}

		},
	methods: {
		jump:function(){
			wx.navigateTo({
				url:"../user_details/user_details"
			})
		},
		requestData(){
			this.$api.getUserInfo().then(res =>{
				console.log(res)
				this.name=res.data.nickName
				this.sno = res.data.sno
				this.head_portrait=res.data.headPortrait
			})
		}
	},
	created(){
		this.requestData()
	}
}
